.app_navbar {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1rem 2rem;
	background: rgba(255, 255, 255, 0.25);
	backdrop-filter: blur(4px);
	-webkit-backdrop-filter: blur(4px);
	position: fixed;
	z-index: 2;
}

.app_navbar-logo {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	img {
		width: 120px;
		height: 90px;
	}
}

.app_navbar-links {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	list-style-type: none;
	li {
		margin: 0 1rem;
		cursor: pointer;
		div {
			width: 5px;
			height: 5px;
			background: transparent;
			margin-bottom: 5px;
			border-radius: 50px;
		}
		a {
			color: var(--gray-color);
			text-decoration: none;
			flex-direction: column;
			text-transform: uppercase;
			font-weight: 500;
			transition: all 0.3s ease-in-out;
			&:hover {
				color: var(--secondary-color);
			}
		}
		&:hover {
			div {
				background: var(--secondary-color);
			}
		}
	}

	@media screen and (max-width: 900px) {
		display: none;
	}
}

.app_navbar-menu {
	width: 35px;
	height: 35px;
	border-radius: 50px;

	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	background: var(--secondary-color);

	svg {
		width: 70%;
		height: 70%;
		color: var(--white-color);
	}
	div {
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		z-index: 5;
		padding: 1rem;
		width: 80%;
		height: 100vh;
		display: flex;
		justify-content: flex-end;
		align-items: end;
		flex-direction: column;
		background: url('../../assests/bgWhite.png');
		background-color: var(--white-color);
		background-size: cover;
		background-repeat: repeat;
		box-shadow: 0 0 20px rgba(168, 168, 168, 0.15);

		svg {
			width: 35px;
			height: 35px;
			color: var(--secondary-color);
			margin: 0.5rem 1rem;
		}
		ul {
			list-style-type: none;
			margin: 0;
			padding: 0;
			height: 100%;
			width: 100%;
			display: flex;
			align-items: start;
			justify-content: flex-start;
			flex-direction: column;
		}
		li {
			margin: 1rem;
			a {
				color: var(--gray-color);
				text-decoration: none;
				font-size: 1rem;
				transition: all 0.3s ease-in-out;
				&:hover {
					color: var(--secondary-color);
				}
			}
		}
    @media screen and (min-width:900px) {
        display: none;
    }    
	}
    @media screen and (min-width:900px) {
        display: none;
    } 
}
